<template>
  <loading :active="isLoading"></loading>
  <div class="container-fluid">
    <div class="mt-3">
      <div class="row" v-if="carts.length > 0">
        <h3 class="mt-3 mb-4">購物清單</h3>
        <div class="col-md-9">
          <table class="table border text-center">
            <thead>
              <tr class="table-brown">
                <th>商品</th>
                <th>數量</th>
                <th>單位</th>
                <th>單價</th>
                <th>小計</th>
                <th></th>
              </tr>
            </thead>
            <tbody class="fw-bold align-middle">
              <tr v-for="item in carts">
                <td>
                  <img
                    :src="item.product.imageUrl"
                    style="width: 72px; height: 72px; object-fit: cover"
                  />
                  <br />
                  <p class="mb-0 fw-bold">
                    {{ item.product.title }}
                  </p>
                </td>
                <td style="max-width: 160px">
                  <div class="input-group border">
                    <button
                      class="btn btn-outline-dark border-0"
                      type="button"
                      @click="minus(item)"
                    >
                      <i class="bi bi-dash"></i>
                    </button>
                    <input
                      type="number"
                      class="form-control text-center"
                      min="1"
                      v-model.number="item.qty"
                      @change="updateCart(item)"
                    />
                    <button
                      class="btn btn-outline-dark border-0"
                      type="button"
                      @click="plus(item)"
                    >
                      <i class="bi bi-plus"></i>
                    </button>
                  </div>
                </td>
                <td>{{ item.product.unit }}</td>
                <td>{{ $filters.currency(item.product.price) }}</td>
                <td>{{ $filters.currency(item.final_total) }}</td>
                <td>
                  <button
                    type="button"
                    class="btn btn-outline-danger"
                    @click="removeCartItem(item.id)"
                  >
                    <i class="bi bi-x-lg"></i>
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
          <div class="d-flex justify-content-between my-2">
            <button
              type="button"
              class="btn btn btn-outline-danger w-50 me-5"
              @click="removeAllCartItem()"
            >
              <i class="bi bi-trash-fill"></i> 全部清空
            </button>
            <router-link to="/products" class="btn btn-outline-secondary w-50"
              ><i class="bi bi-arrow-return-left"></i> 繼續購物</router-link
            >
          </div>
        </div>
        <div class="col-md-3">
          <table class="table border fw-bold">
            <thead class="table-brown text-center">
              <tr>
                <th colspan="2"><span class="fs-4">訂單金額</span></th>
              </tr>
            </thead>
            <tbody class="fs-6">
              <tr>
                <td class="border-0 pt-4">總計</td>
                <td class="border-0 pt-4 text-end">
                  NT${{ $filters.currency(cart.total) }}
                </td>
              </tr>
              <tr v-if="cart.final_total !== cart.total">
                <td class="text-danger">折扣金額</td>
                <td class="text-end text-danger">
                  NT${{ $filters.currency(cart.total - cart.final_total) }}
                </td>
              </tr>
              <tr>
                <td colspan="2" class="border-0 pb-4">
                  優惠碼
                  <input
                    type="text"
                    class="w-100"
                    placeholder="請輸入優惠碼"
                    v-model="coupon_code"
                  />
                  <button
                    type="button"
                    class="btn btn-outline-danger w-100 mt-2"
                    @click="addCouponCode()"
                  >
                    使用優惠券
                  </button>
                </td>
              </tr>
              <tr class="fs-4 border-top">
                <td class="border-0 pt-4">總金額</td>
                <td class="border-0 pt-4 text-end">
                  NT${{ $filters.currency(cart.final_total) }}
                </td>
              </tr>
              <tr>
                <td colspan="2">
                  <router-link
                    to="/order"
                    class="btn btn-outline-brown w-100 my-3"
                    >下一步</router-link
                  >
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <div v-else class="container">
        <div class="row">
          <div
            class="col-md-8  mx-auto border p-5 m-5 rounded bg-brown text-white text-center shadow-lg"
          >
            <h1>購物車為空</h1>
            <router-link
              to="/products"
              class="btn btn-brown border btn-lg w-100 mt-5"
            >
              <i class="bi bi-cart"></i>
              繼續購買
            </router-link>
          </div>

          <div class="row">
            <div class="text-center ">
              <span class="fs-1 fw-bold text-white bg-brown opacity-75 px-5"
                >熱銷商品</span
              >
            </div>
            <div class="container-fluid">

              <AllProduct />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Toast from "@/utils/Toast";
import AllProduct from "@/components/AllProduct.vue";
import emitter from "@/methods/emitter";
export default {
  components: {
    AllProduct,
  },
  data() {
    return {
      cart: {},
      carts: [],
      isLoading: false,
      coupon_code: "",
    };
  },
  inject: ["emitter", "$httpMessageState"],
  methods: {
    getCart() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      this.isLoading = true;
      this.$http.get(url).then((res) => {
        console.log(res);
        this.cart = res.data.data;
        this.carts = res.data.data.carts;
        this.isLoading = false;
      });
    },
    plus(item) {
      item.qty += 1;
      this.updateCart(item);
    },
    minus(item) {
      if (item.qty > 1) {
        item.qty -= 1;
        this.updateCart(item);
      }
    },
    updateCart(item) {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${item.id}`;
      this.isLoading = true;
      const cart = {
        product_id: item.product_id,
        qty: item.qty,
      };
      this.$http.put(url, { data: cart }).then((res) => {
        console.log(res);
        this.isLoading = false;
        this.getCart();
        emitter.emit("get-cart");
      });
    },
    removeCartItem(id) {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${id}`;
      this.isLoading = true;
      this.$http.delete(url).then((res) => {
        // this.$httpMessageState(res, "移除購物車品項");
        Toast.fire({
          icon: 'error',
          title: "移除該品項",
        });
        this.getCart();
        this.isLoading = false;
        emitter.emit("get-cart");
      });
    },
    removeAllCartItem() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/carts`;
      this.isLoading = true;
      this.$http.delete(url).then((res) => {
        Toast.fire({
          icon: 'error',
          title: "全部移除",
        });
        this.getCart();
        this.isLoading = false;
        emitter.emit("get-cart");
      });
    },
    addCouponCode() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/coupon`;
      const coupon = {
        code: this.coupon_code,
      };
      this.$http.post(url, { data: coupon })
      .then((res) => {
        console.log('123',res);
        if(res.data.success){
          this.getCart();
          Toast.fire({
            icon: 'success',
            title: "使用優惠券",
          });
        }else{
          Toast.fire({
          icon: 'error',
          title: "使用失敗",
        });
        }
      })
    },
  },
  created() {
    this.getCart();
  },
};
</script>
